/*----------  COLORS  ----------*/

:root {
    --telefake-blue: #31a8df;
    --telefake-blue-hover: #00d9ff;
    --telefake-transparent-blue: rgba(41, 201, 241, 0.664);
    --grey-background: rgb(37, 58, 70);
    --components-dark-background: linear-gradient(0deg, rgba(13,51,69,1) 0%, rgba(26,36,43,1) 100%);
    --grey: rgb(84, 105, 116);
}


/*----------  Normalize  ----------*/

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',monospace;
}


/*----------  scrollbar  ----------*/

::-webkit-scrollbar {
    width: .6em;
}

::-webkit-scrollbar-thumb {
    border-radius: 9999px;
    background: rgba(41, 201, 241, 0.664);
    background: var(--telefake-transparent-blue);
}

