/*=============================================
=            main container            =
=============================================*/

#main-login-container{
    display: flex;
    background-image: url(/static/media/topography-background.df29649d.jpg);
    background-color: rgba(0, 183, 255, 0.1);
    background-blend-mode: multiply;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}



/*=============================================
=            Login component            =
=============================================*/


#login-container {
    font-family: Allan;
    background: var(--components-dark-background);
    color: white;
    width: 30vw;
    padding: 3%;
    border-radius: 10px;
    box-shadow: -6px 5px 20px 15px rgba(0, 183, 255, 0.3);
}

#login-title {
    font-size: 2rem;
    margin-bottom: 6%;
    text-align: center;
}

#login-input-container {
    display: grid;
    grid-template-columns: 3fr 1fr;
}

#login-input {
    font-family: Caveat;
    display: block;
    background-color: rgb(54, 89, 128);
    box-shadow: inset 0 0 15px 5px rgba(0, 0, 0, .3);
    width: 80%;
    color: aliceblue;
    margin: 0 auto;
    padding: 1% 2%;
    font-size: 1.3rem;
    border-radius: 10px;
    border: .1em solid var(--grey);
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row: 1/1;
    outline: none;
}

#login-input::-webkit-input-placeholder {
    color: rgb(173, 189, 206);
}

#login-input:-ms-input-placeholder {
    color: rgb(173, 189, 206);
}

#login-input::-ms-input-placeholder {
    color: rgb(173, 189, 206);
}

#login-input::placeholder {
    color: rgb(173, 189, 206);
}

#connect-btn {
    width: 2vw;
    grid-row: 1/1;
    grid-column: 2/3;
    border-radius: 2px;
    padding: 2%;
    margin: auto;
}






