/*----------  main-container  ----------*/

.msg-space{
    width: 100%;
    padding: 1%;
    display: flex;
    justify-content: flex-start;
}

.message-container{
    cursor: pointer;
    font-family: Caveat;
    position: relative;
    max-width: 80%;
    line-height: 1.2rem;
    padding: 1.5%;
    border-radius: 15px;
    box-shadow: 0 0 25px 12px rgba(0, 0, 0, 0.1);
    transition: all .3s ease-in-out;
}

.message-container.control-message {
    background-color: var(--grey) !important;
    margin: 0 !important;
    color: aliceblue;
}



.message-container.recivied {
    background-color: aliceblue;
    justify-self: flex-start;
}

.message-container.sent {
    background-color: var(--telefake-blue);
    color: aliceblue;
    margin-left: auto;
    margin-right: 3%;
}

.message-container:hover {
    background-color: var(--telefake-blue-hover);
}



/*----------  Sender  ----------*/

span.sender-name {
    font-family: Allan;
    font-size: 1.2rem;
}

.message-container.control-message span.sender-name {
    display: none;
    content: '';
}

/*----------  Body  ----------*/

.message-body {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.msg-body {
    margin-top: 5%;
    margin-right: 2vh;
    font-size: 1.3rem;
}

.message-container.control-message .msg-body {
    font-family: Allan;
    text-transform: uppercase;
    font-size: 1.1rem;
}

.msg-image img {
    width: 20vw;
    margin-top: 1em;
    border-radius: 5px;
    border: 1px solid white;
}

/*----------  file icon  ----------*/

.msg-icon {
    cursor: pointer;
}

.msg-icon object{
    pointer-events: none;
    width: 1vw;
}



