/*=============================================
=            main-container            =
=============================================*/

.user-status{
    font-family: Allan;
    display: flex;
    background-color: rgb(240, 240, 240);
    padding: 3% 0;
    font-size: 1.2rem;
    text-align: center;
    justify-content: center;
    align-items: center;
}



/*----------  status  ----------*/

.us-status {
    border: 1px solid var(--grey);
    border-radius: 50%;
    padding: 0 2%;
    margin-left: 1%;
    transform: scale(.4);
}

.us-status.online {
    background-color: rgb(134, 235, 39);
    color: rgb(134, 235, 39);
}

.us-status.offline {
    background-color: rgb(247, 47, 47);
    color: rgb(247, 47, 47);
}

